<template>
    <div v-if="step == 0">
        <Post :filters="filters" :게시물="게시물[i]" v-for="(a, i) in 게시물" :key="i"/>
    </div>
    <!-- v-if 와 v-for은 같이 못 쓰기 때문에 div로 다시 만들면 사용가능 -->
    <!-- 필터선택페이지 -->
    <dvi v-if="step == 1">
        <div @click="$state.commit('increase')" :class="choiceFilter" class="upload-image" :style = "{backgroundImage : `url(${image})`}"></div>
        <!-- `background-image : url(${image})`도 똑같은 문법 -->
        <div class="filters">
            <FilterBox :image="image" :filter="filter" v-for="filter in filters" :key="filter">
                {{filter}}
            </FilterBox>
        </div> 
    </dvi>

    <!-- 글작성페이지 -->
    <div v-if="step == 2">
        <div :class="choiceFilter" class="upload-image" :style = "{backgroundImage : `url(${image})`}"></div>
        <div class="write">
            <textarea @input="$emit('write', $event.target.value)" class="write-box">write!</textarea>
        </div>
    </div>
</template>

<script>

import Post from "./Post.vue";
import FilterBox from "./FilterBox.vue";

export default {
  name: "containerApp",
  data() {
      return {
          filters : [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
"inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
"reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
          choiceFilter : '',
      }
  },
  props: {
    게시물: Array,
    step : Number,
    image : String,
    content : String,
  },
  mounted() {
    this.emitter.on('applyFilter', (a)=>{
      this.choiceFilter = a
    })
  },
  components: {
    Post,
    FilterBox,
  },
};
</script>

<style>
    .upload-image{
    width: 100%;
    height: 450px;
    background: cornflowerblue;
    background-size : cover;
    }
    .filters{
    overflow-x:scroll;
    white-space: nowrap;
    }
    .filter-1 {
    width: 100px;
    height: 100px;
    background-color: cornflowerblue;
    margin: 10px 10px 10px auto;
    padding: 8px;
    display: inline-block;
    color : white;
    background-size: cover;
    }
    .filters::-webkit-scrollbar {
    height: 5px;
    }
    .filters::-webkit-scrollbar-track {
    background: #f1f1f1; 
    }
    .filters::-webkit-scrollbar-thumb {
    background: #888; 
    border-radius: 5px;
    }
    .filters::-webkit-scrollbar-thumb:hover {
    background: #555; 
    }
    .write-box {
    border: none;
    width: 90%;
    height: 100px;
    padding: 15px;
    margin: auto;
    display: block;
    outline: none;
    }
</style>