<template>
  <div class="header">
    <ul class="header-button-left">
      <li @click="step--">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">올리기</li>
    </ul>

    <img src="./assets/logo.png" class="logo" />
  </div>

  <Container :게시물="게시물" :step="step" :image="image" @write="content = $event"/>
  <button v-if="step == 0" @click="more">더보기</button>

  <div v-if="step == 0" class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" accept="image/*" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

  <!-- 
  <div v-if="step == 0">내용1</div>
  <div v-if="step == 1">내용2</div>
  <div v-if="step == 2">내용3</div>
  <button @click="step = 0">버튼1</button>
  <button @click="step = 1">버튼2</button>
  <button @click="step = 2">버튼3</button> -->
  <!-- "="는 같다라는 뜻 / "=="는 일치한다라는 뜻 
  그렇기에 step == 0 은 step이 0과 일치한다 / step = 0 은 step은 0이다.-->

</template>


<script>

import Container from "./components/Container.vue";
import Data from "./data.js";
import axios from "axios";
axios.post()

export default {
  name: "App",
  data() {
    return {
      게시물 : Data,
      더보기 : 0,
      step : 0,
      image : '',
      content : '',
      choiceFilter : '',
    }
  },
  mounted() {
    this.emitter.on('applyFilter', (a)=>{
      this.choiceFilter = a
    })
  },
  components: {
    Container,
  },
  methods : {
    more() {
      axios.get(`https://codingapple1.github.io/vue/more${this.더보기}.json`)
      // 문자 안에 오브젝트형 자료형을 넣을 때는 벡틱(`)과 ${}을 이용한다.
      .then(result => {
        this.게시물.push(result.data);
        this.더보기++;
      })
    },

    upload(e) {
      let file = e.target.files;
      let url = URL.createObjectURL(file[0]);
      this.image = url;
      this.step++;
    },

    publish() {
      let myPost = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.image,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.content,
        filter: this.choiceFilter,
      };
      this.게시물.unshift(myPost);
      // unshift 는 myPost를 게시물에 끼워넣어줌
      this.step = 0;
    }
  },
}
      // more()이라는 함수 안에 get 요청 성공시 
      // 실행할 코드가 들어있는 함수가 있음. 
      // 함수 안에 있는 함수를 콜백함수라 한다.
      // 콜백함수 사용할 때, function(){}이 아닌 () => {} 를 사용한 이유는
      // this.를 사용할 때 일반 function은 this를 재정의하지만 애로우funtion은 
      // 바깥의 this를 그대로 가져오기 때문에 vue 사용시 애로우funtion이 더 좋다
</script>

