import { createStore } from 'vuex'

const store = createStore({
  state(){
    return {
      likes : 0,
      clickLikes : true,
      likesWord : '좋아요',
    }
  },
  mutations : {
      increase(state) {
          if(state.clickLikes == true) {
            state.likes ++;
            state.clickLikes = false;
            state.likesWord = '취소';
          } else {
            state.likes --;
            state.clickLikes = true;
            state.likesWord = '좋아요';
          }
      },
    //   decrease(state) {
    //       state.likes --;
    //   }
  }
})

export default store